@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Italic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-BoldItalic.ttf") format("truetype");
  font-weight: bold;
  font-style: italic;
}
* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.pricing-section {
  display: flex;
  justify-content: center;
  background: linear-gradient(180deg, #f5f5fa 0%, #f2dad5 44.15%, #a1abd6 100%);
}
.pricing-section__inner {
  max-width: 1366px;
  width: 100%;
  min-height: 100vh;
}
.pricing-section .header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 50px;
}
.pricing-section .header__menu-icon {
  width: 24px;
  height: 16px;
  fill: rgb(35, 29, 79);
}
.pricing-section .header__logo-wrapp {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-weight: normal;
  color: rgb(35, 29, 79);
  text-decoration: none;
  font-weight: normal;
  font-size: 28px;
}
.pricing-section .header__logo-icon {
  width: 23px;
  height: 30px;
}
.pricing-section .header__btn {
  padding: 10px 30px;
  background-color: rgb(187, 107, 217);
  border-radius: 25px;
  border: none;
  font-size: 14px;
  line-height: 1.8;
  color: #fff;
  font-weight: 500;
  transition: background-color 0.5s;
  cursor: pointer;
}
.pricing-section .header__btn:hover {
  background-color: rgb(168, 71, 204);
}
.pricing-section .main {
  padding: 41px 16px 60px;
}
.pricing-section .main__inner {
  max-width: 926px;
  margin: 0 auto;
  width: 100%;
}
.pricing-section .main__desc {
  display: flex;
  justify-content: space-between;
  align-items: end;
}
.pricing-section .main__header {
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 25px;
}
.pricing-section .main__header-title {
  font-weight: 400;
  font-size: 40px;
  line-height: 60px;
  color: rgb(35, 29, 79);
}
.pricing-section .main__header-text {
  color: rgb(132, 129, 153);
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}
.pricing-section .main__btns {
  display: flex;
  margin-left: 122px;
  background-color: #f5f5fa;
  border-radius: 25px;
}
.pricing-section .main__btns-item {
  background-color: #f5f5fa;
  border: none;
  padding: 13px 24px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.83px;
  cursor: pointer;
  border-radius: 25px;
  transition: 0.5s;
  color: rgb(35, 29, 79);
}
.pricing-section .main__btns-item.active {
  background-color: rgb(187, 107, 217);
  color: rgb(255, 255, 255);
  border-radius: 25px;
  box-shadow: 0px 6px 12px 0px rgba(82, 67, 194, 0.16);
}
.pricing-section .main__btns-item.active:hover {
  background-color: rgb(168, 71, 204);
}
.pricing-section .pricing-cards {
  display: flex;
  justify-content: space-between;
  gap: 34px;
  padding: 40px 60px 40px 40px;
  background-color: rgba(255, 255, 255, 0.64);
  margin: 64px 0 69px;
  border-radius: 28px;
}
.pricing-section .pricing-cards .pricing-card {
  text-align: start;
  position: relative;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.pricing-section .pricing-cards .pricing-card__inner {
  position: relative;
}
.pricing-section .pricing-cards .pricing-card__price, .pricing-section .pricing-cards .pricing-card__feature, .pricing-section .pricing-cards .pricing-card__description {
  color: rgb(132, 129, 153);
}
.pricing-section .pricing-cards .pricing-card__price {
  font-size: 17px;
  font-weight: normal;
  padding-bottom: 20px;
}
.pricing-section .pricing-cards .pricing-card__price-number {
  font-weight: bold;
  font-size: 36px;
  color: rgb(35, 29, 79);
  line-height: 46px;
}
.pricing-section .pricing-cards .pricing-card__title {
  color: rgb(35, 29, 79);
  font-weight: normal;
  font-size: 28px;
  line-height: 42px;
  padding-bottom: 10px;
}
.pricing-section .pricing-cards .pricing-card__description {
  font-size: 15px;
  line-height: 22.5px;
  font-weight: normal;
  padding-bottom: 20px;
}
.pricing-section .pricing-cards .pricing-card__feature {
  padding-bottom: 10px;
  display: flex;
  align-items: start;
  font-size: 15px;
  line-height: 22.5px;
  font-weight: normal;
}
.pricing-section .pricing-cards .pricing-card__feature-icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.pricing-section .pricing-cards .pricing-card__choose-btn {
  padding: 10px 20px;
  background-color: rgb(35, 29, 79);
  color: white;
  border: none;
  font-size: 15px;
  font-weight: normal;
  border-radius: 25px;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.5s;
}
.pricing-section .pricing-cards .pricing-card__choose-btn:hover {
  background-color: rgb(168, 71, 204);
}
.pricing-section .pricing-cards .pricing-card--highlight {
  background-color: rgb(35, 29, 79);
  transform: scale(1.2);
  position: relative;
  bottom: 30px;
  border-radius: 25px;
  box-shadow: 0px 42px 34px 0px rgba(82, 67, 194, 0.3);
  padding: 0 30px;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__most-popular {
  color: rgb(187, 107, 217);
  font-size: 10px;
  line-height: 15px;
  letter-spacing: 0.83px;
  font-weight: bold;
  background: rgb(64, 56, 121);
  display: inline-block;
  padding: 6px 14px;
  border-radius: 20px;
  position: absolute;
  right: 12px;
  top: 20px;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__price {
  padding-top: 67px;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__price-number {
  color: #fff;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__title,
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__description,
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__price,
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__feature {
  color: #fff;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__choose-btn {
  background-color: rgb(187, 107, 217);
  margin-bottom: 20px;
  padding: 8px 20px;
}
.pricing-section .pricing-cards .pricing-card--highlight .pricing-card__choose-btn:hover {
  background-color: rgb(168, 71, 204);
}
.pricing-section .hidden {
  display: none;
}

@media (max-width: 790px) {
  .pricing-section .header__inner {
    padding: 32px 24px;
  }
  .pricing-section .pricing-cards {
    flex-direction: column;
    gap: 48px;
    padding: 32px 8px 8px;
    margin: 32px 0 20px;
  }
  .pricing-section .pricing-cards .pricing-card {
    padding: 0 20px;
  }
  .pricing-section .pricing-cards .pricing-card__inner {
    position: relative;
  }
  .pricing-section .pricing-cards .pricing-card__choose-btn {
    margin-top: 24px;
  }
  .pricing-section .pricing-cards .pricing-card--highlight {
    transform: scale(1);
    bottom: 0px;
    padding: 0 20px;
  }
  .pricing-section .pricing-cards .pricing-card--highlight .pricing-card__most-popular {
    padding: 6px 14px;
    border-radius: 20px;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    width: auto;
    margin: auto;
    text-align: center;
  }
  .pricing-section .pricing-cards .pricing-card--highlight .pricing-card__choose-btn {
    margin-top: 24px;
  }
}
@media (max-width: 600px) {
  .pricing-section .header__btn {
    display: none;
  }
  .pricing-section .main__desc {
    align-items: center;
    gap: 32px;
    flex-direction: column;
  }
  .pricing-section .main__btns {
    margin-left: 0;
  }
}